.headerContainer {
  height: 56px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  align-content: center;
  flex-flow: row wrap;
  background-color: #fff;
  border-bottom: 1px solid rgb(203, 203, 203);
}

.reddit-mini-logo {
  width: 35px;
  height: 30px;
  display: flex;
  margin-left: 10px;
}
.reddit-mini-logo .logo-tail {
  position: relative;
  top: 20px;
  left: 2px;
  width: 15px;
  height: 10px;
  background-color: rgb(175, 118, 249);
  background: linear-gradient(to bottom right, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0) 50%, rgb(255, 69, 0) 50%, rgb(255, 69, 0) 100%);
  border-radius: 10px;
}
.reddit-mini-logo .logo-circle {
  position: absolute;
  width: 30px;
  height: 30px;
  background-color: rgb(255, 69, 0);
  border-radius: 100%;
  font-size: 1.5rem;
}

.bear {
  position: relative;
  left: 2.95px;
  top: 1px;
}

.title {
  display: none;
  color: rgb(255, 69, 0);
  margin: 0px;
  font-weight: 800;
  font-size: 30px;
  font-family: "Outfit", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", sans-serif;
}

@media (min-width: 1000px) {
  .title {
    display: flex;
  }
}